// src/api/orderManagement.ts
import request from '../utils/request'

// 获取订单列表
export function getOrderList(api: string, page: number, pageSize: number, search: Record<string, any> = {}) {
    return request({
        url: api,
        method: 'get',
        params: {
            page,
            page_size: pageSize,
            search: JSON.stringify(search)
        }
    });
}

// 获取订单详情
export function getOrderDetail(api: string) {
    return request({
        url: api,
        method: 'get'
    });
}

// 更新订单状态
export function updateOrderStatus(api: string, data: Record<string, any> = {}) {
    return request({
        url: api,
        method: 'post',
        data
    });
}

// 关闭订单
export function closeOrder(api: string, id: number) {
    return request({
        url: api,
        method: 'post',
        data: { id }
    });
}

// 申请退款
export function refundOrder(api: string, data: Record<string, any> = {}) {
    return request({
        url: api,
        method: 'post',
        data
    });
}

// 导出订单数据
export function exportOrderData(api: string, search: Record<string, any> = {}) {
    return request({
        url: api,
        method: 'post',
        data: { search },
        responseType: 'blob'
    });
}

// 获取订单统计
export function getOrderStatistics(api: string, search: Record<string, any> = {}) {
    return request({
        url: api,
        method: 'get',
        params: {
            search: JSON.stringify(search)
        }
    });
}


